//BirdCtrl.ts
import { _decorator, Component, Node, RigidBody, v3, input, Input, EventMouse, PhysicsSystem, Collider, ICollisionEvent, SphereCollider } from 'cc';
import { EnumGameStatus, GameManager } from './GameManager';
const { ccclass, property } = _decorator;

@ccclass('BirdCtrl')
export class BirdCtrl extends Component {
    //鸟所在的节点
    @property(Node)
    Bird:Node = null!
    //GameManager脚本
    @_decorator.type(GameManager)
    GameManagerScript:GameManager = null!
///////////////////////////////////////////////
    start() {
        input.on(Input.EventType.MOUSE_DOWN,this.onMouseClick,this);//开启输入监听
        PhysicsSystem.instance.gravity=v3(0,-500,0);//设置重力
        this.Bird.getComponent(RigidBody).linearDamping=0.1;//设置速度阻尼
        //this.Bird.getComponent(Collider).on('onTriggerEnter',this.onHit,this)//原来的开启碰撞检测
        let coms=this.Bird.getComponents(SphereCollider);//两个球体碰撞箱组件
        coms[0].on('onCollisionEnter',this.onHit,this);//第一个碰撞箱，不使用触发器，对障碍物进行判断
        coms[1].on('onTriggerEnter',this.getScore,this)//第二个碰撞箱，使用触发器，对分数进行判断
        
    }
///////////////////////////////////////////////
    onMouseClick(event: EventMouse){//鼠标输入
        this.Bird.getComponent(RigidBody).setLinearVelocity(v3(0,300,0));//更改速度为向上
    }
    onHit(event: ICollisionEvent){//撞上柱子
        console.log("Hit");
        this.GameManagerScript.gameStatus = EnumGameStatus.die;//碰撞后死亡

    }
    getScore(event: ICollisionEvent){
        console.log("Scored");
        this.GameManagerScript.score++;
    }
}

